
const {ccclass, property} = cc._decorator;
enum Mtype {
    VERTICAL_P,
    VERTICAL_N, 
    HORIZONTAL_P,
    HORIZONTAL_N,
  }
@ccclass
export default class NewClass extends cc.Component {

    @property({type:cc.Node,tooltip:""})
    lubg1:cc.Node = null;

    @property({type:cc.Node,tooltip:""})
    lubg2:cc.Node = null;

    @property({
        type:cc.Integer,
        tooltip:"",
        min:0,
        max:1000,
        step:50,
        slide:true,
    })
    speed = 200;

    @property({ type: cc.Enum(Mtype),tooltip:"VERTICAL_P:  VERTICAL_N: HORIZONTAL_P: HORIZONTAL_N:" })
    mtype: Mtype = Mtype.VERTICAL_P;

    isRunmap = false;//
    cur_lubg:cc.Node = null;
    luOffset = 0;
    // onLoad () {}

    start () {
        this.cur_lubg = this.lubg1;
        this.setbgfix();
        // this.isRunmap = true;
    }

    // fixBg() {
    //     let frameSize: cc.Size = cc.winSize // 
    //     let designResolution: cc.Size = cc.Canvas.instance.designResolution
    //     // console.log(" ====frameSize: ", frameSize);
    //     // console.log(" ====designResolution: ", designResolution);
        
    //     let designSize: cc.Size = cc.size(designResolution.width, designResolution.height)  // cc.Canvas.instance.designResolution
    //     let frameAspectRatio: number = frameSize.width / frameSize.height
    //     let screenSize: cc.Size = { ...designSize } as cc.Size
        
    //     // console.log(" ====screenSize: ", screenSize);

    //     let ratio: number = this.bg.width / this.bg.height
    //     // console.log(" ====ratio: ", ratio, "   ===frameAspectRatio: ", frameAspectRatio);
    //     if (ratio > frameAspectRatio) {
    //         let width = this.bg.width * screenSize.height / this.bg.height
    //         this.bg.width = width
    //         this.bg.height = screenSize.height
    //     } else {
    //         let scale = frameSize.width / designSize.width;
    //         this.bg.width = designSize.width * scale;
    //         this.bg.height = designSize.height * scale;

    //     }
    //     // console.log(" ====: ", this.bg.width, "   ===frameAspectRatio: ", this.bg.height);

    //     this.bg.x = 0
    //     this.bg.y = 0
    // }

    setbgfix(){
        let frameSize: cc.Size = cc.winSize;
        cc.log("frameSize",frameSize)
        let designResolution: cc.Size = cc.Canvas.instance.designResolution
        let designSize: cc.Size = cc.size(designResolution.width, designResolution.height)
        let frameAspectRatio: number = frameSize.width / frameSize.height
        let screenSize: cc.Size = { ...frameSize } as cc.Size

        let ratio: number = this.node.width / this.node.height
        if (ratio > frameAspectRatio) {
            let width = this.node.width * screenSize.height / this.node.height
            this.lubg1.width = width
            this.lubg1.height = screenSize.height
            this.lubg2.width = width
            this.lubg2.height = screenSize.height
            switch (this.mtype) {
                case Mtype.VERTICAL_P:
                    this.lubg2.y = screenSize.height;
                    this.luOffset = screenSize.height;
                    break;
                case Mtype.VERTICAL_N:
                    this.lubg2.y = -screenSize.height;
                    this.luOffset = screenSize.height;
                    break;
                case Mtype.HORIZONTAL_P:
                    this.lubg2.x = width;
                    this.luOffset = width;
                    break;
                case Mtype.HORIZONTAL_N:
                    this.lubg2.x = -width;
                    this.luOffset = width;
                    break;
                default:
                    break;
            }
        } else {
            let height = this.node.height * screenSize.width / this.node.width
            this.lubg1.width = screenSize.width
            this.lubg1.height = height
            this.lubg2.width = screenSize.width
            this.lubg2.height = height
            switch (this.mtype) {
                case Mtype.VERTICAL_P:
                    this.lubg2.y = height;
                    this.luOffset = height;
                    break;
                case Mtype.VERTICAL_N:
                    this.lubg2.y = -height;
                    this.luOffset = height;
                    break;
                case Mtype.HORIZONTAL_P:
                    this.lubg2.x = screenSize.width;
                    this.luOffset = screenSize.width;
                    break;
                case Mtype.HORIZONTAL_N:
                    this.lubg2.x = -screenSize.width;
                    this.luOffset = screenSize.width;
                    break;
                default:
                    break;
            }
        }
    }

    runlubg(dt){
        var s = dt * this.speed;
        
        switch (this.mtype) {
            case Mtype.VERTICAL_P:
                this.lubg1.y -= s;
                this.lubg2.y -= s;
                if (this.cur_lubg.y <= -this.luOffset) {
                    if(this.cur_lubg == this.lubg2) {
                        this.lubg2.y = this.lubg1.y + this.luOffset;
                        this.cur_lubg = this.lubg1;
                    }
                    else {
                        this.lubg1.y = this.lubg2.y + this.luOffset;
                        this.cur_lubg = this.lubg2;
                    }
                }
                break;
            case Mtype.VERTICAL_N:
                this.lubg1.y += s;
                this.lubg2.y += s;
                if (this.cur_lubg.y >= this.luOffset) {
                    if(this.cur_lubg == this.lubg2) {
                        this.lubg2.y = this.lubg1.y - this.luOffset;
                        this.cur_lubg = this.lubg1;
                    }
                    else {
                        this.lubg1.y = this.lubg2.y - this.luOffset;
                        this.cur_lubg = this.lubg2;
                    }
                }
                break;
            case Mtype.HORIZONTAL_P:
                this.lubg1.x -= s;
                this.lubg2.x -= s;
                if (this.cur_lubg.x <= -this.luOffset) {
                    if(this.cur_lubg == this.lubg2) {
                        this.lubg2.x = this.lubg1.x + this.luOffset;
                        this.cur_lubg = this.lubg1;
                    }
                    else {
                        this.lubg1.x = this.lubg2.x + this.luOffset;
                        this.cur_lubg = this.lubg2;
                    }
                }
                break;
            case Mtype.HORIZONTAL_N:
                this.lubg1.x += s;
                this.lubg2.x += s;
                if (this.cur_lubg.x >= this.luOffset) {
                    if(this.cur_lubg == this.lubg2) {
                        this.lubg2.x = this.lubg1.x - this.luOffset;
                        this.cur_lubg = this.lubg1;
                    }
                    else {
                        this.lubg1.x = this.lubg2.x - this.luOffset;
                        this.cur_lubg = this.lubg2;
                    }
                }
                break;
            default:
                break;
        }
        
    }
    
    update (dt) {
        if (this.isRunmap) {
            this.runlubg(dt);
        }
    }
}
