import { kit } from "../../../../kit/kit";
import Tigo from "../Tigo";
import TigoGrabState from "./TigoGrabState";

/**
 * tigo
 */
export default class TigoIdleState implements kit.fsm.State<Tigo>{
    private static readonly IDLE: string = 'L2_W0_day1_1_tigo_b_daiji';
    private static readonly TICK_OUT: string = 'L2_W0_day1_1_tigo_b_nachu';
    private static readonly AIM: string = 'L2_W0_day1_1_tigo_b_yaobai';

    entity: Tigo;

    onMessage(entity: kit.fsm.Entity, telegram: kit.fsm.Telegrams): boolean {
        throw new Error("Method not implemented.");
    }
    enter(data?: any): Promise<any> {
        this.entity.idleNode.active = true;
        this.entity.stageWinNode.active = false;
        this.entity.completeNode.active = false;
        this.entity.arm.active = false;
        // tigo
        this.entity.arm.active = true;
        this.entity.node.on(cc.Node.EventType.TOUCH_START, this.onTouch, this);
        // Util.addCoin_midAndMenu(this.entity.node.parent,"");
        return Promise.resolve();
    }
    execute(data?: any): void {
        throw new Error("Method not implemented.");
    }
    exit(data?: any): void {
        this.entity.node.off(cc.Node.EventType.TOUCH_START, this.onTouch, this);
    }

    private onTouch(event: cc.Event): void {
        this.entity.stateMachine.ChangeState(TigoGrabState);
    }

}