import { kit } from "../../../../kit/kit";
import Arm from "../Arm";
import Events from "../Events";

// 
export default class ArmIdleState implements kit.fsm.State<Arm>{
    private static readonly LEFT: number = 70;
    private static readonly RIGHT: number = -70;

    public entity: Arm;

    onMessage(entity: kit.fsm.Entity, telegram: kit.fsm.Telegrams): boolean {
        throw new Error("Method not implemented.");
    }

    enter(data?: any): Promise<void> {
        this.entity.outNode.active = true;
        this.entity.inNode.active = false;
        kit.manager.Event.on(Events.GAME_STOP, this.onStop, this);
        kit.manager.Event.on(Events.GAME_STAR, this.onStop, this);
        kit.manager.Event.on(Events.GUID_SEND, this.onSend, this);
        // AudioManager.playLoopMusic('SE_zhuashuiguo02_ytz', null, false);
        return Promise.resolve();
    }

    execute(dt?: any): void {
        this.updateArmAngle();
        this.entity.node.angle += this.entity.rotateSpeed * dt;
    }

    exit(data?: any): void {
        kit.manager.Event.off(Events.GAME_STOP, this.onStop, this);
        kit.manager.Event.off(Events.GAME_STAR, this.onStop, this);
        kit.manager.Event.off(Events.GUID_SEND, this.onSend, this);
    }

    private onStop(): void {
        this.entity.isGuide = !this.entity.isGuide;
        cc.log(`isGuide${this.entity.isGuide}`);
    }

    private updateArmAngle(): void {
        if (this.entity.node.angle >= ArmIdleState.LEFT) {
            this.entity.rotateSpeed = -this.entity.rotateSpeed;
            this.entity.node.angle = ArmIdleState.LEFT;
        } else if (this.entity.node.angle <= ArmIdleState.RIGHT) {
            this.entity.rotateSpeed = Math.abs(this.entity.rotateSpeed);
        }
    }

    private onSend(): void {
        kit.manager.Event.emit(Events.GUID_SEND);
    }



}