cc.macro.ENABLE_TRANSPARENT_CANVAS = true;
const { ccclass, property } = cc._decorator;
@ccclass
export default class NewClass extends cc.Component {

    @property(cc.VideoPlayer)
    video: cc.VideoPlayer = null;

    @property(cc.Prefab)
    gameNodes: cc.Prefab[] = [];

    @property(cc.Node)
    bg: cc.Node = null;

    @property(cc.Node)
    play_btn: cc.Node = null;

    @property(cc.Node)
    controlNode: cc.Node = null;

    @property(cc.Node)
    controlBtn: cc.Node = null;

    @property(cc.Node)
    pauseNode: cc.Node = null;

    @property(cc.Node)
    playNode: cc.Node = null;

    @property(cc.Node)
    PRe: cc.Node = null;

    @property(cc.Slider)
    videoProgress: cc.Slider = null;

    @property(cc.Label)
    videoTime: cc.Label = null;

    @property(cc.Label)
    maxTime: cc.Label = null;

    // @property(Number)
    progressMax = 0

    current = 0
    gameIndex = 0
    timeScript = []
    timeOutScript = []
    showGameIndex = -1           //video
    isPlaying = true
    isControl = false
    isUpSpeed = true

    onLoad() {
        this.timeScript = [];
        this.timeOutScript = [];
        this.isPlaying = true;
        this.current = 0;
    }

    start() {
        // this.regisEvent()
        this.video.node.active = true;
        console.log("video showout")
    }

    regisEvent() {
        this.video.node.on("ready-to-play", this.vedioReady, this);
        this.video.node.on("meta-loaded", this.vedioLoaded, this);
        this.video.node.on("playing", this.vedioPlaying, this);
        this.video.node.on("paused", this.vedioPaused, this);
        this.video.node.on("completed", this.vedioCompleted, this);
        this.videoProgress.node.on("slide", this.changeProgress, this);
        this.controlBtn.on("touchstart", this.control_btn_start, this);
        this.controlBtn.on("touchend", this.control_btn_end, this);
        this.controlBtn.on("touchcancel", this.control_btn_end, this);
    }

    vedioCompleted() {
        console.log("open_vedio_Completed");
        window.webframe.Notify.trigger(  // app   
            "open_vedio_Completed"
        );
    }

    vedioReady() {
        console.log("vedioReady");
        // this.play_btn.active = true;
        // this.bg.getChildByName("load").active = false
        this.node.active=false;
        window.webframe.Notify.trigger(  // app   
            "open_vedio_Completed"
        );
    }

    vedioPlaying() {
        console.log("vedioPlaying");
    }

    vedioPaused() {
        console.log("vedioPaused");
    }

    vedioLoaded() {
        console.log("vedioLoaded");
        console.log("video totaltime ==== " + this.video.getDuration());
        this.progressMax = this.video.getDuration();
        this.maxTime.string = "/" + this.secondToDate(this.progressMax);
    }

    secondToDate(result) {
        var m = Math.floor((result / 60 % 60)) < 10 ? '0' + Math.floor((result / 60 % 60)) : Math.floor((result / 60 % 60));
        var s = Math.floor((result % 60)) < 10 ? '0' + Math.floor((result % 60)) : Math.floor((result % 60));
        return result = m + ":" + s;
    }

    vedioPause(event, data) {
        console.log("vedioPaused!!!!!!!!!!");
        this.video.pause();
        this.pauseNode.active = false;
        this.playNode.active = true;
    }

    vedioPlay(event, data) {
        this.video.play();
        this.pauseNode.active = true;
        this.playNode.active = false;
    }

    play() {
        console.log("play video url ==== " + this.video.remoteURL);
        //  AudioManager.playMusic(AudioManager.JINGYIN)
        this.video.play();
        this.bg.active = false;
        this.scheduleOnce(() => this.setProgress(), 0.2)
        this.controlNode.active = true
    }

    setProgress() {
        this.video.currentTime += 0.01
        this.videoProgress.progress = this.video.currentTime / this.progressMax
        // this.videoProgress.getComponent(cc.ProgressBar).progress = this.videoProgress.progress;
        this.PRe.getComponent(cc.ProgressBar).progress = this.videoProgress.progress;
        this.videoTime.string = this.secondToDate(this.video.currentTime);
    }

    showVideoActive() {
        this.video.node.active = true
        this.video.currentTime += 0.1;
    }

    setVideotime() {
        this.video.node.active = true
        this.video.currentTime = this.timeOutScript[this.gameIndex]
    }

    game_right() {
        if (this.node.getChildByName("game")) {
            this.node.getChildByName("game").destroy();
        }
        this.gameIndex++;
        console.log("game_right === " + this.gameIndex);
        //  TigoManager.callAppToCollect(this.gameIndex, 1005, 0);
        // this.video.currentTime += 0.05;
        this.video.play();
        this.isPlaying = true;
        this.controlNode.active = true;
    }

    addGame(index) {
        if (index == this.showGameIndex) {
            this.video.node.active = false
        }
        //   GameData.SetddManageMentIndex(index)
        var game = cc.instantiate(this.gameNodes[index]);
        this.node.addChild(game);
        game.name = "game";
    }

    UpSpeed() {
        //   TigoManager.callAppToSpeed()
    }

    changeProgress() {
        this.PRe.getComponent(cc.ProgressBar).progress = this.videoProgress.progress;
        // this.videoProgress.getComponent(cc.ProgressBar).progress = this.videoProgress.progress;
        this.video.currentTime = this.videoProgress.progress * this.progressMax;
        this.game_check();
        this.checkSpeed()
    }

    checkSpeed() {
        if (this.isUpSpeed) {
            let able = this.video.currentTime / this.progressMax
            if (able >= 0.7) {
                console.log("== changeProgress====")
                this.UpSpeed()
                this.isUpSpeed = false
            }
        }
    }

    game_check() {
        let vt = this.video.currentTime;
        let ts = this.timeScript;
        if (vt < ts[0]) {
            this.gameIndex = 0;
        }
        else if (vt > ts[0] && vt < ts[1]) {
            this.gameIndex = 1;
        }
        else if (vt > ts[1] && vt < ts[2]) {
            this.gameIndex = 2;
        } else if (vt > ts[2] && vt < ts[3]) {
            this.gameIndex = 3;
        } else if (vt > ts[3] && vt < ts[4]) {
            this.gameIndex = 4;
        }
        cc.log("gameIndex ====== " + this.gameIndex);
    }

    rec_btn_call(event, data) {
        //   TigoManager.callAppToRecord();
    }

    stop_btn_call(event, data) {
        //   TigoManager.callAppToStopRecord();
    }

    control_btn_start(event, data) {
        this.isControl = true;
    }

    control_btn_end(event, data) {
        this.game_check();
        cc.log("gameIndex === " + this.gameIndex);
        this.isControl = false;
    }

    update(dt) {
        if (this.isControl == false) {
            this.videoProgress.progress = this.video.currentTime / this.progressMax;
            // this.videoProgress.getComponent(cc.ProgressBar).progress = this.videoProgress.progress;
            this.PRe.getComponent(cc.ProgressBar).progress = this.videoProgress.progress;
            this.videoTime.string = this.secondToDate(this.video.currentTime);
        }
        this.checkSpeed()
        if (this.isPlaying && this.isControl == false) {

            this.current = this.video.currentTime;

            if (this.current >= this.timeScript[this.gameIndex] && this.current < this.timeScript[this.gameIndex] + 0.05) {
                cc.log("add game!!!!  " + this.gameIndex);
                this.isPlaying = false;
                this.video.pause();
                this.addGame(this.gameIndex);
                this.controlNode.active = false;
            }
        }
        else {
            return;
        }

    }

    onDisable() {
        //   EventMsg.off("setVideotime", this.setVideotime, this,);
        //  EventMsg.off("game_right", this.game_right, this);
    }
}