import { kit } from "../../../kit/kit";
import ArmIdleState from "./arm/ArmIdleState";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Arm extends cc.Component implements kit.fsm.Entity {

    @property(cc.Node)
    armTop: cc.Node = null;

    @property(cc.Node)
    outNode: cc.Node = null;

    @property(cc.Node)
    inNode: cc.Node = null;

    @property(cc.Node)
    lineNode: cc.Node = null;

    public stateMachine: kit.fsm.StateManager<Arm>;
    public sid: string = "Arm";
    public rotateSpeed: number = 60; //
    public isGuide: boolean = false

    start() {
        this.stateMachine = new kit.fsm.StateManager<Arm>(this);
        this.init();
    }

    private init(): void {
        this.stateMachine.ChangeState(ArmIdleState);
        cc.tween(this.lineNode).repeatForever(cc.tween(this.lineNode).to(1, { opacity: 64 }).to(1, { opacity: 200 })).start();
        // cc.tween(this.lineNode).repeatForever(tween);
    }

    update(dt: number): void {
        if (!this.isGuide) {
            this.stateMachine.Update(dt);
        }
    }

}
