import { kit } from "../../../../kit/kit";
import Arm from "../Arm";
import Events from "../Events";
import ArmInState from "./ArmInState";

// 
export default class ArmOutState implements kit.fsm.State<Arm>{
    private static readonly MAX_LENGTH: number = 380;
    entity: Arm;

    onMessage(entity: kit.fsm.Entity, telegram: kit.fsm.Telegrams): boolean {
        throw new Error("Method not implemented.");
    }

    enter(data?: any): Promise<any> {
        this.entity.outNode.active = true;
        this.entity.inNode.active = false;
        kit.manager.Event.on(Events.GRAB_ACTION_ON_COLLISION, this.onCollision, this);
        kit.manager.Audio.playEffect('SE_zhuashuiguo03_ytz');
        return Promise.resolve();
    }

    execute(data?: any): void {
        if (this.entity.armTop.y >= ArmOutState.MAX_LENGTH) {
            this.entity.stateMachine.ChangeState(ArmInState);
        } else {
            this.entity.armTop.y += 4.5;
        }
    }

    exit(data?: any): void {
        kit.manager.Event.off(Events.GRAB_ACTION_ON_COLLISION, this.onCollision, this);
    }

    // 
    private onCollision(event: any): void {
        let fruitNode: cc.Node = event.data.node;
        if (event.data.node.name.indexOf('banan') > -1) {       // 
            fruitNode = fruitNode.getChildByName('bananaItem');
            fruitNode.active = true;
        }
        fruitNode.x = fruitNode.y = 0;
        fruitNode.parent = this.entity.inNode.parent;
        fruitNode.zIndex = -1;
        this.entity.stateMachine.ChangeState(ArmInState, fruitNode);
    }

}