import { kit } from "../../../kit/kit";
import Point from "../../../Scripts/common/Point";
import WordTool from "../../../Tools/Word/scripts/WordTool";
import Level1State from "./level/Level1Sate";
import LevelGlobalState from "./level/LevelGloabelState";
import Tigo from "./Tigo";
// import Level3State from "./level/Level3Sate";

const { ccclass, property } = cc._decorator;

@ccclass
export default class GrabFruit extends cc.Component implements kit.fsm.Entity {

    @property(cc.Node)
    bgsNode: cc.Node = null;

    @property(cc.Node)
    monsterNode: cc.Node = null;

    @property(cc.Node)
    birdNode: cc.Node = null;

    @property(cc.Node)
    tigo: cc.Node = null;

    @property(cc.Prefab)
    tigoPrefab: cc.Prefab = null;

    @property(WordTool)
    word: WordTool = null;

    public stateMachine: kit.fsm.StateManager<GrabFruit>;
    public camera: cc.Camera;
    private isPlaying: boolean = false;

    async onLoad() {
        Point.instance.init("", 3);
        Point.instance.gameStart();
        this.stateMachine = new kit.fsm.StateManager<GrabFruit>(this);
        this.stateMachine.ChangeState(Level1State);
        this.stateMachine.AddGlobalState(LevelGlobalState);
        this.camera = this.node.parent.getChildByName('Main Camera').getComponent(cc.Camera);
        var manager = cc.director.getCollisionManager();
        manager.enabled = true;
        await kit.system.timer.init();
        await kit.manager.Audio.init();
        // manager.enabledDebugDraw = true;
        // manager.enabledDrawBoundingBox = true;

        // kit.system.timer.doOnce(3000, () => {
        //     kit.manager.Audio.playEffect('mangoes', false);
        // })

        cc.log('cc.sys.isMobile', cc.sys.isMobile);
        if (!cc.sys.isMobile) {
            this.init();
        } else {
            cc.log('7');
            this.scheduleOnce(() => {
                cc.log('7');
                this.init();
            }, 7);
        }
    }

    public onDestroy() {
        kit.system.timer.release();
        this.stateMachine.release();
    }

    private init(): void {
        if (!this.isPlaying) {
            cc.log("bgm");
            this.isPlaying = true;
            kit.manager.Audio.playMusic('bgm');
            this.tigo.getComponent(Tigo).init();
        }

    }


}
