import { kit } from "../../../../kit/kit";
import Arm from "../Arm";
import ArmOutState from "../arm/ArmOutState";
import Events from "../Events";
import Tigo from "../Tigo";
import TigoIdleState from "./TigoIdleState";

/**
 * 
 */
export default class TigoGrabState implements kit.fsm.State<Tigo>{
    entity: Tigo;
    private arm: Arm;

    onMessage(entity: kit.fsm.Entity, telegram: kit.fsm.Telegrams): boolean {
        throw new Error("Method not implemented.");
    }
    enter(data?: any): Promise<any> {
        this.entity.idleNode.active = true;
        this.entity.stageWinNode.active = false;
        this.entity.completeNode.active = false;
        this.arm = this.entity.arm.getComponent<Arm>(Arm);
        this.arm.stateMachine.ChangeState(ArmOutState);
        kit.manager.Event.on(Events.GRAB_ACTION_COMPLETE, this.crabComplete, this);
        return Promise.resolve();
    }
    execute(data?: any): void {
        throw new Error("Method not implemented.");
    }
    exit(data?: any): void {
        kit.manager.Event.on(Events.GRAB_ACTION_COMPLETE, this.crabComplete, this);
    }

    private crabComplete(event: any): void {
        this.entity.stateMachine.ChangeState(TigoIdleState);
    }

}