import { kit } from "../../../kit/kit";
import TigoGlobalState from "./tigo/TigoGloabelState";
import TigoGuideState from "./tigo/TigoGuideState";
import TigoWinState from "./tigo/TigoWinState";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Tigo extends cc.Component implements kit.fsm.Entity {

    @property(cc.Prefab)
    armPrefab: cc.Prefab = null;

    @property(cc.Node)
    idleNode: cc.Node = null;

    @property(cc.Node)
    stageWinNode: cc.Node = null;

    @property(cc.Node)
    completeNode: cc.Node = null;

    @property(cc.Node)
    guide: cc.Node = null;

    public stateMachine: kit.fsm.StateManager<Tigo>;
    public arm: cc.Node;
    public sid: string = "tigo";

    onLoad() {
        this.stateMachine = new kit.fsm.StateManager<Tigo>(this);
        this.guide.active = false;
        this.idleNode.active = true;
        this.stageWinNode.active = false;
        this.completeNode.active = false;
    }

    public init(): void {
        this.arm = cc.instantiate(this.armPrefab);
        this.arm.x = 60;
        this.arm.y = 128;
        this.node.getChildByName('logo').addChild(this.arm, -1);
        this.stateMachine.AddGlobalState(TigoGlobalState);
        this.stateMachine.AddGlobalState(TigoGuideState);
        this.stateMachine.ChangeState(TigoWinState);
    }

}
