import { kit } from "../../../../kit/kit";
import Point from "../../../../Scripts/common/Point";
import Config from "../config/config";
import Events from "../Events";
import GrabFruit from "../GrabFruit";

// 
export default class Level3State implements kit.fsm.State<GrabFruit>{
    private static readonly MIN_LENGTH: number = 0;
    private static readonly CURRENT_LEVEL: number = 2;
    private static readonly MONSTER_IN: string = 'L2_W0_day1_1_monster_in';
    private static readonly MONSTER_OUT: string = 'L2_W0_day1_1_monster_out';
    private static readonly MONSTER_OUT2: string = 'L2_W0_day1_1_monster_out2';
    private static readonly MONSTER_DILE: string = 'L2_W0_day1_1_monster_daiji';

    public entity: GrabFruit;

    private current: number = 0;
    private times: number = 0;

    onMessage(entity: kit.fsm.Entity, telegram: kit.fsm.Telegrams): boolean {
        throw new Error("Method not implemented.");
    }

    enter(data?: any): Promise<any> {
        Point.instance.roundStart(3);
        cc.tween(this.entity.bgsNode).to(1, { x: -Config.SCENE_WIDTH }).call(() => {

        }).start();
        kit.system.timer.doLoop(5000, this.guidScale, this);
        kit.manager.Event.on(Events.GRAB_SUCCESS, this.onGrabComplete, this);
        kit.manager.Event.on(Events.GRAB_ACTION_ON_COLLISION, this.onCollision, this);
        kit.manager.Event.on(Events.FRUIT_DOWN_COMPLETE, this.onFruitDownComplete, this);
        kit.manager.Event.on(Events.GRAB_ACTION_COMPLETE, this.onActionComplete, this);

        return Promise.resolve();
    }

    execute(data?: any): void {
    }

    exit(data?: any): void {
        kit.system.timer.clearTimer(this.guidScale);
        kit.manager.Event.off(Events.GRAB_SUCCESS, this.onGrabComplete, this);
        kit.manager.Event.off(Events.GRAB_ACTION_ON_COLLISION, this.onCollision, this);
        kit.manager.Event.off(Events.FRUIT_DOWN_COMPLETE, this.onFruitDownComplete, this);
        kit.manager.Event.off(Events.GRAB_ACTION_COMPLETE, this.onActionComplete, this);

    }

    private onGrabComplete(event: any): void {
        this.current += 1;
        cc.log(`target count:${this.current}`)
        kit.manager.Event.emit(Events.GAME_SCORE, { index: this.current, level: 3 });
        if (this.current >= Config.LEVEL_TARGET_FRUIT_NUMBER) {
            // 
            Point.instance.roundEnd(3, this.times);
            Point.instance.gameEnd();
            kit.manager.Event.emit(Events.SCENE_CHANGE, { level: 3 });
            kit.system.timer.clearTimer(this.guidScale);
            this.palyMonsterAnimation();
            kit.manager.Event.emit(Events.GAME_STAGE_COMPLETE, { level: 3 });
            kit.system.timer.doOnce(1000, () => {
                kit.manager.Event.emit(Events.GAME_COMPLETE);
            })
        }
    }

    // 
    private palyMonsterAnimation(): void {
        let sp: sp.Skeleton = this.entity.monsterNode.getComponent<sp.Skeleton>('sp.Skeleton');
        kit.system.timer.doOnce(1500, () => {
            kit.manager.Audio.playEffect('SE_zhuashuiguo06_ytz');
            sp.setAnimation(0, Level3State.MONSTER_IN, false);
            sp.setCompleteListener(() => {
                sp.setAnimation(0, Level3State.MONSTER_DILE, false);
                sp.setCompleteListener(() => {
                    sp.setAnimation(0, Level3State.MONSTER_OUT, false);
                    sp.setCompleteListener(null);
                })
            })
        })
    }
    // 
    private guidScale(): void {
        let list: cc.Node[] = this.entity.bgsNode.getChildByName(`scene${3}`).children;
        list.forEach((element: cc.Node, index: number) => {
            if (element.name.indexOf('coco') > -1) {
                // cc.log('element.name', element.name)
                cc.tween(element).to(0.2, { scale: 0.5 }).to(0.2, { scale: 1 }).start();
            }
        })
    }
    private onCollision(): void {
        this.entity.word.Init(this.entity.word.coconut);
        kit.manager.Audio.playEffect('coconut');
    }

    private onFruitDownComplete(): void {
        this.entity.word.Init(this.entity.word.coconuts);
        kit.manager.Audio.playEffect('coconuts', false, () => {
            kit.manager.Audio.playEffect('SE_zhuashuiguo04_ytz')
        });
    }

    private onActionComplete(): void {
        this.times += 1;
    }
}