import { kit } from "../../../../kit/kit";
import Arm from "../Arm";
import Events from "../Events";
import ArmIdleState from "./ArmIdleState";

// 
export default class ArmInState implements kit.fsm.State<Arm>{
    private static readonly MIN_LENGTH: number = 0;
    public entity: Arm;
    private target: any;

    onMessage(entity: kit.fsm.Entity, telegram: kit.fsm.Telegrams): boolean {
        throw new Error("Method not implemented.");
    }
    enter(data?: any): Promise<any> {
        this.target = data;
        this.entity.outNode.active = false;
        this.entity.inNode.active = true;
        kit.manager.Audio.playEffect('SE_zhuashuiguo03_ytz');

        return Promise.resolve();
    }
    execute(data?: any): void {
        if (this.entity.armTop.y <= ArmInState.MIN_LENGTH) {
            this.entity.stateMachine.ChangeState(ArmIdleState);
        } else {
            this.entity.armTop.y -= 4.5;
        }
    }

    exit(data?: any): void {
        if (this.target) {
            (this.target as cc.Node).parent.removeChild(this.target);
        }
        kit.manager.Event.emit(Events.GRAB_ACTION_COMPLETE);
        if (this.target) {
            kit.manager.Event.emit(Events.GRAB_SUCCESS);
        }
    }

}