import { kit } from "../../../../kit/kit";
import Events from "../Events";
import Tigo from "../Tigo";
import TigoGrabState from "./TigoGrabState";

/**
 * 
 */
export default class TigoGuideState implements kit.fsm.State<Tigo>{
    entity: Tigo;

    onMessage(entity: kit.fsm.Entity, telegram: kit.fsm.Telegrams): boolean {
        throw new Error("Method not implemented.");
    }
    enter(data?: any): Promise<any> {
        kit.system.timer.doOnce(6550, () => {
            kit.manager.Audio.playEffect('pleaseTap')
            kit.manager.Event.emit(Events.GAME_STOP);
            this.entity.guide.active = true;
            this.entity.guide.on(cc.Node.EventType.TOUCH_START, this.onTouch, this);
        }, this);
        return Promise.resolve();
    }
    execute(data?: any): void {
        throw new Error("Method not implemented.");
    }
    exit(data?: any): void {
        this.entity.guide.active = false;
        kit.manager.Event.emit(Events.GAME_STAR);
        this.entity.guide.off(cc.Node.EventType.TOUCH_START, this.onTouch, this);
    }

    private onTouch(): void {
        this.entity.stateMachine.RemoveGlobalState(TigoGuideState);
        this.entity.stateMachine.ChangeState(TigoGrabState);
        kit.manager.Event.emit(Events.GAME_GUID_COMPLETE)
    }

}