import { kit } from "../../../../kit/kit";
import GrabFruit from "../GrabFruit";

// 
export default class LevelGlobalState implements kit.fsm.State<GrabFruit>{
    public entity: GrabFruit;

    onMessage(entity: kit.fsm.Entity, telegram: kit.fsm.Telegrams): boolean {
        throw new Error("Method not implemented.");
    }

    enter(data?: any): Promise<any> {
        let delay: number = 9000 + Math.random() * 14000;
        cc.log('delay', delay)
        setTimeout(() => {
            this.egg();
        }, delay);
        return Promise.resolve();
    }

    execute(data?: any): void {
    }

    exit(data?: any): void {
        kit.system.timer.clearTimer(this.egg);
    }

    private egg(): void {
        if (this.entity.birdNode) {
            this.entity.birdNode.x = 1000;
            cc.tween(this.entity.birdNode).to(9, { x: -1000 }).start();
            let delay: number = 9000 + Math.random() * 14000;
            cc.log('delay', delay)
            setTimeout(() => {
                this.egg();
            }, delay);
        }
    }
}