class GameData {
    node_path = [
        {
            "nodePath": "grabFruit",
        }
    ]
    record_num: string = ""
    totalStar_num: number = 0
    isFrist: boolean = true
    isTest = false;
    gid = [
        [1, 2, 3],
        [4, 5, 6]
    ];

    private static _Instance: GameData;

    public static get getInstance(): GameData {
        if (!this._Instance) {
            this._Instance = new GameData()
        }
        return this._Instance
    }

    getStarNum() {
        return this.totalStar_num;
    }

    setStarNum(num: any) {
        this.totalStar_num = Number(num);
    }

    addStarNum(num) {
        this.totalStar_num += num;
    }

    setIsFirst(able: boolean) {
        console.log("setIsFirst ================" + able);
        this.isFrist = able
    }

    getIsFirst() {
        console.log("getIsFirst ================" + this.isFrist);
        return this.isFrist
    }

    getNodePath(page) {
        if (this.node_path[page].nodePath) {
            return this.node_path[page].nodePath
        } else {
            return this.node_path[0].nodePath
        }
    }
}
export default GameData.getInstance
