export default class TigoCompleteState implements kit.fsm.State<Tigo>{
    entity: Tigo;
    onMessage(entity: kit.fsm.Entity, telegram: kit.fsm.Telegrams): boolean {
        throw new Error("Method not implemented.");
    }
    enter(data?: any): Promise<any> {
        this.entity.idleNode.active = false;
        this.entity.stageWinNode.active = false;
        this.entity.completeNode.active = true;
        this.entity.arm.active = false;
        kit.manager.Audio.playEffect('SE_zhuashuiguo05_ytz')
        cc.tween(this.entity.node).to(3, { x: 1200 }).call(() => {
            this.entity.stateMachine.exitCurrentState();
        }).start()
        for (let i = 1; i < 4; i++) {
            this.entity.node.getChildByName('logo').getChildByName(`box${i}`).active = false;
        }
        return Promise.resolve();
    }
    execute(data?: any): void {
        throw new Error("Method not implemented.");
    }
    exit(data?: any): void {
        // Util.addCoin_midAndMenu(this.entity.node.parent, "");
        window.webframe.Notify.trigger("entryMenu",true);
    }


}