import { kit } from "../../../../kit/kit";
import Events from "../Events";
import Tigo from "../Tigo";
import TigoCompleteState from "./TigoCompleteState";
import TigoPoseState from "./TIgoPoseState";

/**
 * 
 */
export default class TigoWinState implements kit.fsm.State<Tigo>{
    private static readonly IDLE: string = 'L2_W0_day1_1_tigo_z_daiji';
    private static readonly HAPPY: string = 'L2_W0_day1_1_tigo_z_huanhu';
    entity: Tigo;
    onMessage(entity: kit.fsm.Entity, telegram: kit.fsm.Telegrams): boolean {
        throw new Error("Method not implemented.");
    }
    enter(data?: any): Promise<any> {
        this.entity.idleNode.active = false;
        this.entity.stageWinNode.active = true;
        this.entity.completeNode.active = false;
        this.entity.arm.active = false;
        let sp: sp.Skeleton = this.entity.stageWinNode.getChildByName("L2_W0_day1_1_tigo_z").getComponent<sp.Skeleton>('sp.Skeleton');
        let sp2: sp.Skeleton = this.entity.stageWinNode.getChildByName("L2_W0_day1_1_hopper_z").getComponent<sp.Skeleton>('sp.Skeleton');
        if ((!data) || (data.level < 3)) {
            sp.setAnimation(0, TigoWinState.IDLE, true);
            sp2.setAnimation(0, 'L2_W0_day1_1_hopper_z_daiji', true);
            kit.system.timer.doOnce(1000, () => {
                sp.setAnimation(0, TigoWinState.HAPPY, true);
                sp2.setAnimation(0, 'L2_W0_day1_1_hopper_z_huahu', true);
                kit.system.timer.doOnce(3000, () => {
                    this.entity.stateMachine.ChangeState(TigoPoseState);
                })
            })
        } else {
            sp.setAnimation(0, TigoWinState.IDLE, true);
            sp2.setAnimation(0, 'L2_W0_day1_1_hopper_z_daiji', true);
            kit.system.timer.doOnce(1000, () => {
                sp.setAnimation(0, TigoWinState.HAPPY, true);
                sp2.setAnimation(0, 'L2_W0_day1_1_hopper_z_huahu', true);
            })
        }
        kit.manager.Event.on(Events.GAME_COMPLETE, this.onLearnComplete, this);
        return Promise.resolve();
    }
    execute(data?: any): void {
        throw new Error("Method not implemented.");
    }
    exit(data?: any): void {
        kit.manager.Event.off(Events.GAME_COMPLETE, this.onLearnComplete, this);
    }

    private onLearnComplete(): void {
        cc.tween(this).delay(2.6).call(() => {
            this.entity.stateMachine.ChangeState(TigoCompleteState);
        }).start()
    }

}