import { kit } from "../../../../kit/kit";
import Events from "../Events";
import Tigo from "../Tigo";
import TigoWinState from "./TigoWinState";

/**
 * tigo
 */
export default class TigoGlobalState implements kit.fsm.State<Tigo>{
    entity: Tigo;
    onMessage(entity: kit.fsm.Entity, telegram: kit.fsm.Telegrams): boolean {
        throw new Error("Method not implemented.");
    }
    enter(data?: any): Promise<any> {
        kit.manager.Event.on(Events.GAME_STAGE_COMPLETE, this.onStageComplete, this);
        return Promise.resolve();
    }
    execute(data?: any): void {
        throw new Error("Method not implemented.");
    }
    exit(data?: any): void {
        kit.manager.Event.off(Events.GAME_STAGE_COMPLETE, this.onStageComplete, this);
    }

    private onStageComplete(event: any): void {
        let level: number = event.data.level;
        kit.system.timer.doOnce(3000, () => {
            this.entity.node.getChildByName('logo').getChildByName(`box${level}`).getChildByName('item').active = true
        })
        this.entity.stateMachine.ChangeState(TigoWinState, { level: level });
    }

    private onScore(event: any): void {
        let level: number = event.data.level;
    }

}