import { kit } from "../../../kit/kit";
import Config from "./config/config";
import Events from "./Events";

const { ccclass, property } = cc._decorator;

@ccclass
export default class ProgressBar extends cc.Component {

    @property(cc.ProgressBar)
    bar: cc.ProgressBar = null;

    private currentLevelNode: cc.Node;
    private showIndex: number = 1;

    onLoad() {
        this.init();
        kit.manager.Event.on(Events.GAME_STAGE_COMPLETE, this.onStageComplete, this);
        kit.manager.Event.on(Events.GAME_SCORE, this.onScore, this);
    }

    onDestroy() {
        kit.manager.Event.off(Events.GAME_STAGE_COMPLETE, this.onStageComplete, this);
        kit.manager.Event.off(Events.GAME_SCORE, this.onScore, this);
    }

    private init(): void {
        this.hideAllLevel();
        this.hideAllItem(1);
        this.currentLevelNode = this.node.getChildByName(`level${1}`);
        this.currentLevelNode.active = true;
        this.bar.progress = 0;
    }

    // private onGameComplete(): void {
    //     if (this.showIndex < 4) {
    //         let node: cc.Node = this.node.getChildByName(`level${this.showIndex}`);
    //         node.active = true;
    //         let list: cc.Node[] = [];
    //         for (let i = 1; i < 4; i++) {
    //             let item: cc.Node = node.getChildByName(`itemBg${i}`).getChildByName('item');
    //             item.active = false;
    //             list.push(item);
    //         }
    //         this.move2Bottom(list);
    //         this.showIndex += 1;
    //     } else {
    //         kit.manager.Event.emit(Events.GAME_LEARN_COMPLETE);
    //     }
    // }

    private move2Bottom(list: cc.Node[]): void {
        this.scheduleOnce(() => {
            let effectTime: number = 1;
            list.forEach((element: cc.Node, index: number) => {
                element.active = true;
                let p: any = Config.COMPLETE_STAGE_POINTS[index];
                let targetPoint: cc.Vec3 = new cc.Vec3(p.x, p.y);
                cc.tween(element).to(effectTime, { position: targetPoint }).start();
            });
            cc.tween(this).delay(2.5).call(() => {
                list.forEach((element: cc.Node) => {
                    element.active = false;
                })
            })
            kit.system.timer.doOnce(effectTime * 1000, () => {
                kit.manager.Event.emit(Events.FRUIT_DOWN_COMPLETE);
            })
        }, 0.3)
    }

    private onStageComplete(event: any): void {
        let level: number = event.data.level;
        let node: cc.Node = this.node.getChildByName(`level${level}`);
        node.active = true;
        let list: cc.Node[] = [];
        for (let i = 1; i < 4; i++) {
            let item: cc.Node = node.getChildByName(`itemBg${i}`).getChildByName('item');
            item.active = false;
            list.push(item);
        }
        this.move2Bottom(list);
        kit.system.timer.doOnce(3000, () => {
            this.updateLevel(level);
        })
    }

    private onScore(event: any): void {
        let index: number = event.data.index;
        this.updateProgress(index);
    }

    private updateProgress(index: number): void {
        let item: cc.Node = this.currentLevelNode.getChildByName(`itemBg${index}`).getChildByName('item');
        item.active = true;
        let target: number = 0;
        // 
        switch (index) {
            case 1:
                // this.bar.progress = 0;
                target = 0;
                break;
            case 2:
                // this.bar.progress = 0.5;
                target = 0.5;
                break;
            case 3:
                // this.bar.progress = 1;
                target = 1;
                break;
        }
        cc.tween(this.bar).to(0.2, { progress: target }).start();
    }

    private updateLevel(level: number): void {
        this.hideAllItem(level);
        if (level + 1 <= Config.LEVEN_NUMBER) {
            this.hideAllItem(level + 1);
            this.currentLevelNode.active = false;
            this.currentLevelNode = this.node.getChildByName(`level${level + 1}`);
            this.currentLevelNode.active = true;
            this.bar.progress = 0;
        }
    }

    private hideAllItem(level: number): void {
        let node: cc.Node = this.node.getChildByName(`level${level}`);
        for (let i = 1; i < 4; i++) {
            node.getChildByName(`itemBg${i}`).getChildByName('item').active = false;
        }
    }

    private hideAllLevel(): void {
        for (let i = 1; i < 4; i++) {
            this.node.getChildByName(`level${i}`).active = false;
        }
    }

}
